import css from './PrivateRoute.module.css'
import { Navigate , Outlet } from "react-router-dom"
import { useSelector} from "react-redux"
import { getAuth } from "../../Redux/Auth/auth-selectot";

const PrivateRoute = () =>{                    //         
  
   const{isLoading , token}= useSelector(getAuth)
   // const isLoading = useSelector(isUserLogin)  //     isLoading i  token    true  false       true
   if(!isLoading && token){                             //  isLoading = false              
      return <span className={css.loader}></span>
   }
   if(!isLoading && !token){
      return <Navigate to='/login'/>
   }
   return <Outlet/>                     //     <Outlet/>   isLoading = true  .      PrivateRoute

}
export default PrivateRoute